* {
    padding: 0;
    margin: 0;
    border: 0;
}
*,
*:before,
*:after {
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
}
:focus,
:active {
    outline: none;
}
a:focus,
a:active {
    outline: none;
}

nav,
footer,
header,
aside {
    display: block;
}

html,
body {
    height: 100%;
    width: 100%;
    font-size: 100%;
    line-height: 1;
    font-size: 14px;
    -ms-text-size-adjust: 100%;
    -moz-text-size-adjust: 100%;
    -webkit-text-size-adjust: 100%;
}
input,
button,
textarea {
    font-family: inherit;
}

input::-ms-clear {
    display: none;
}
button {
    cursor: pointer;
}
button::-moz-focus-inner {
    padding: 0;
    border: 0;
}
a,
a:visited {
    text-decoration: none;
}
a:hover {
    text-decoration: none;
}
ul li {
    list-style: none;
}
img {
    vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-size: inherit;
    font-weight: 400;
}

a,
button {
    color: inherit;
}

.App {
    padding-top: 20px;
}

.App > .container {
    display: grid;
    grid-template-areas:
        "header header"
        "nav main";
    grid-template-columns: 1fr 2fr;
    grid-template-rows: 80px 1fr;
    gap: 20px;
    max-width: calc(1440px + 40px);
    width: 100%;
    margin: 0 auto;
    padding: 0 20px;
}

.main {
    grid-area: main;
}

.logo {
    text-transform: uppercase;
    font-size: 32px;
    font-weight: bolder;
}

a {
    transition: 0.5s;
}

a:hover {
    opacity: 0.5;
}
